.card {
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 166px;
  height: 358px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
}

.card__product {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
